import React, { useContext } from 'react';
import { AppContext } from "../context";
import { Link, useParams } from 'react-router-dom';

const Home = () => {
    const { array } = useContext(AppContext)

    let cocteyl = array.map(el => {
        return (
            <div key={el.id} className="cocktails-center col-4">
                <div className="cocktails">
                    <article className='cocktail'>
                        <div className='img-container'>
                            <img src={el.strDrinkThumb} alt={el.strDrinkThumb} />
                        </div>
                        <div className='cocktail-footer'>
                            <h3>{el.strDrink}</h3>
                            <h4>{el.strGlass}</h4>
                            <p>{el.strAlcoholic}</p>
                            <Link className="btn btn-primary btn-details" to="/about/:id">details</Link>
                        </div>
                    </article>
                </div>
            </div>
        )
    })
    return (
        <div>
            <section className="search">
                <form className="search-form">
                    <div className="form-control">
                        <label for="name">search your favorite cocktail</label>
                        <input type="text" name="name" id="name" />
                    </div>
                </form>
            </section>
            <div className="row mt-5">
                {cocteyl}
            </div>
        </div>
    );
};

export default Home;



