import React, {useContext} from 'react';
import {Link} from "react-router-dom";
import {useParams} from "react-router-dom"
import {AppContext} from "../context";


const PhotoCom = () => {
    const params = useParams()
    const {array} = useContext(AppContext)
    console.log(array);
    const currentData = ( ( el ) => {
        return (el.idDrink === params.id)
    })
    let data = array.map(el =>{
        return(
            <section key={el.idDrink} className='section cocktail-section'>
                <Link to='' className='btn btn-primary'>
                    back home
                </Link>
                <h2 className='section-title'>{el.strDrink}</h2>
                <div className='drink'>
                    <img src={el.strDrinkThumb} alt="a" />
                    <div className='drink-info'>
                        <p>
                            <span className='drink-data'>name :</span> {el.strDrink}
                        </p>
                        <p>
                            <span className='drink-data'>category :</span> {el.strCategory}
                        </p>
                        <p>
                            <span className='drink-data'>info :</span> {el.strAlcoholic}
                        </p>
                        <p>
                            <span className='drink-data'>glass :</span> {el.strGlass}
                        </p>
                        <p>
                            <span className='drink-data'>instructons :</span> {el.strInstructions}
                        </p>
                    </div>
                </div>
            </section>
        )
    })


    console.log(currentData)
    return (
        <div>
            {data}
        </div>
    );
};

export default PhotoCom;
